import { ThemeProvider } from 'styled-components';

const theme = {
  red: '#FF0000',
  black: '#393939',
  grey: '#3A3A3A',
  lightgrey: '#E1E1E1',
  offWhite: '#EDEDED',
  maxWidth: '1000px',
  bs: '0 12px 24px 0 rgba(0,0,0,0.09)'
};

const Page = props => {
  return (
    <ThemeProvider theme={theme}>
      <div>{props.children}</div>
    </ThemeProvider>
  );
};

export default Page;
